<template>
  <div id="app">
    <header>
      <h1>Beasky Music App</h1>
    </header>
    <section class="player">
        <h2 class="song-title">{{current.title}} - <span>{{current.artist}}</span></h2>

        <section class="playlist">
          <h5>ALBUM</h5>
          <h3>{{current.album}}</h3>
          <!-- <h3>Beasky Playlist</h3> -->
          <div class="controls">
          <i class="fa fa-backward prev" @click="prev"></i>
          <i class="fa fa-play play" v-if="!isPlaying" @click="play"></i>
          <i class="fa fa-pause pause" v-else @click="pause"></i>
          <i class="fa fa-forward next" @click="next"></i>
        </div>
          <button v-for="song in songs" :key="song.src" @click="play(song)"
          :class="(song.src == current.src) ? 'song playing' : 'song'">
            {{song.title}} - {{song.artist}}
          </button>
        </section>
      </section>
  </div>
</template>

<script>

export default {
  name: 'App',
  data () {
    return {
      current: {},
      index: 0,
      isPlaying: false,
      songs: [
        {
        title: '1. Do Better',
        artist: 'Stormzy',
        album: 'HEAVY IS THE HEAD',
        src: require('./assets/Do-Better.mp3')
      },
      {
        title: '2. Bebo',
        artist: 'Burna Boy',
        album: 'TWICE AS TALL',
        src: require('./assets/Bebo.mp3')
      },
      {
        title: '3. Goodbyes',
        artist: 'Post Malone',
        album: 'GOODBYES',
        src: require('./assets/Goodbyes.mp3')
      },
      {
        title: '4. La La',
        artist: 'Davido ft Ckay',
        album: 'A BETTER TIME',
        src: require('./assets/La.mp3')
      },
      {
        title: '5. Intentions',
        artist: 'Justin Bieber (feat. Quavo)',
        album: 'CHANGES',
        src: require('./assets/Intentions.mp3')
      },
      {
        title: '6. Raggamuffin',
        artist: 'Koffe',
        album: 'RAPTURE',
        src: require('./assets/Raggamuffin.mp3')
      }
      ],
      player: new Audio()
    }
  },
  methods: {
    play(song) {
      console.log(song)
      if(typeof song.src != "undefined") {
        this.current = song;

        this.player.src = this.current.src;
      }
      this.player.play();
      this.player.addEventListener('ended', function() {
        if(this.index > this.songs.length - 1) {
        this.index = 0;
      }

      this.current = this.songs[this.index];
      this.play(this.current);
      }.bind(this));
      this.isPlaying = true;
      console.log(this.isPlaying)
    },
    pause() {
      this.player.pause();
      this.isPlaying = false;

    },
    next () {
      this.index++;
      if(this.index > this.songs.length - 1) {
        this.index = 0;
      }

      this.current = this.songs[this.index];
      this.play(this.current);
    },
    prev () {
      this.index--;
      if(this.index < 0) {
        this.index = this.songs.length - 1;
      }

      this.current = this.songs[this.index];
      this.play(this.current);
    }
  },
  created() {
    this.current = this.songs[this.index];
    this.player.src = this.current.src;
    // this.player.play();
  }
}
</script>

